import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';

@Injectable()
export class NhTabService {
    onTabSelected = new Subject();

    constructor() {
    }

    selectTab(tabId: any) {
        this.onTabSelected.next(tabId);
    }
}
