import {
    AfterViewInit, Component, ComponentFactoryResolver, ElementRef, EventEmitter, Input, OnInit, Output, Renderer2,
    ViewChild,
    ViewEncapsulation
} from '@angular/core';
import { LocationStrategy, PathLocationStrategy, Location } from '@angular/common';
import { NhTabService } from './nh-tab.service';
import { NhTabHostDirective } from './nh-tab-host.directive';

@Component({
    selector: 'nh-tab-panel',
    template: `
        <ng-content></ng-content>
        <ng-template nh-tab-host></ng-template>
    `,
    providers: [Location, { provide: LocationStrategy, useClass: PathLocationStrategy }],
    encapsulation: ViewEncapsulation.None
})

export class NhTabPanelComponent implements OnInit, AfterViewInit {
    @ViewChild(NhTabHostDirective) tabHostDirective: NhTabHostDirective;
    @Input() id: string;
    @Input() title: string;
    @Input() active: boolean;
    @Input() icon: string;
    @Input() showClose: boolean;
    @Input() url: string;

    @Output() onSelectTab = new EventEmitter();

    constructor(private location: Location,
        private _componentFactoryResolver: ComponentFactoryResolver,
        private el: ElementRef, private renderer2: Renderer2,
        private tabService: NhTabService) {
        this.tabService.onTabSelected.subscribe(tabId => {
            if (tabId === this.id) {
                this.renderer2.addClass(this.el.nativeElement, 'active');
                if (this.url) {
                    this.location.go(this.url);
                }
            } else {
                this.renderer2.removeClass(this.el.nativeElement, 'active');
            }
        });
    }

    ngOnInit() {
    }

    ngAfterViewInit() {
        if (this.active) {
            this.renderer2.addClass(this.el.nativeElement, 'active');
        } else {
            this.renderer2.removeClass(this.el.nativeElement, 'active');
        }
    }

    loadComponent<T>(component: any): T {
        const componentFactory = this._componentFactoryResolver.resolveComponentFactory(component);
        const viewContainerRef = this.tabHostDirective.viewContainerRef;
        viewContainerRef.clear();

        const componentRef = viewContainerRef.createComponent(componentFactory);
        return <T>componentRef.instance;
    }
}
