import {
    AfterContentInit, Component, ContentChildren, ElementRef, EventEmitter, KeyValueDiffers, OnInit, Output, QueryList,
    SimpleChanges,
    ViewChild,
    ViewChildren,
    ViewEncapsulation
} from '@angular/core';
import { LocationStrategy, PathLocationStrategy, Location } from '@angular/common';
import { NhTabPanelComponent } from './nh-tab-panel.component';
import { Title } from '@angular/platform-browser';
import { NhTabService } from './nh-tab.service';
import { NHTab, NHTabTitle } from './nh-tab.model';
import * as _ from 'lodash';

@Component({
    selector: 'nh-tab',
    template: `
        <ul class="nh-tab-title-container" #nhTabTitleContainer>
            <li class="nh-tab-title"
                *ngFor="let tabItem of listTabTitle"
                [class.active]="tabItem.active"
                (click)="selectTab(tabItem)"
                [attr.title]="tabItem.title">
                <i class="nh-tab-title-icon" [ngClass]="tabItem.icon" *ngIf="tabItem.icon"></i>
                <span class="nh-tab-title-content">{{tabItem.title}}</span>
                <i class="fa fa-times btn-close-tab" *ngIf="tabItem.showClose" (click)="closeTab(tabItem)"></i>
            </li>
        </ul>
        <div class="nh-tab-content">
            <ng-content></ng-content>
            <nh-tab-panel *ngFor="let tabItem of listTabDynamic"
                          [id]="tabItem.id"
                          [active]="tabItem.active"
                          [url]="tabItem.url"
                          [title]="tabItem.title"
                          [icon]="tabItem.icon"
                          [showClose]="tabItem.showClose"
                          [class.active]="tabItem.active == true"
            ></nh-tab-panel>
        </div>
    `,
    styleUrls: ['./nh-tab.component.scss'],
    providers: [Location, { provide: LocationStrategy, useClass: PathLocationStrategy }],
    encapsulation: ViewEncapsulation.None
})

export class NhTabComponent implements OnInit, AfterContentInit {
    @ContentChildren(NhTabPanelComponent) nhTabPanelComponents: QueryList<NhTabPanelComponent>;
    @ViewChildren(NhTabPanelComponent) nhTabPanelComponentsDynamic: QueryList<NhTabPanelComponent>;
    @ViewChild('nhTabTitleContainer') nhTabTitleContainer: ElementRef;
    @Output() onCloseTab = new EventEmitter();
    listTabTitle = [];
    listTabDynamic = [];

    constructor(private location: Location,
        private title: Title,
        private tabService: NhTabService) {

    }

    ngOnInit() {
    }

    ngAfterContentInit() {
        this.nhTabPanelComponents.map((nhTabPanel: NhTabPanelComponent) => {
            this.listTabTitle = [...this.listTabTitle, new NHTabTitle(nhTabPanel.id, nhTabPanel.title, nhTabPanel.active, nhTabPanel.url, nhTabPanel.icon)];
        });
    }

    addTab(tabItem: NHTab) {
        this.title.setTitle(tabItem.title);
        if (tabItem.distince) {
            // Kiểm tra xem tab đã tồn tại chưa.
            const tabInfo = _.find(this.listTabDynamic, (tab: NHTab) => {
                return tabItem.id === tab.id;
            });

            if (tabInfo) {
                this.setTabActive(tabInfo);
            } else {
                this.appendTab(tabItem);
            }
        } else {
            this.appendTab(tabItem);
        }
    }

    selectTab(tab: NHTab) {
        this.title.setTitle(tab.title);
        this.tabService.selectTab(tab.id);

        // Update lại trạng thái active của tab về false
        this.listTabTitle.forEach((tabItem: NHTab) => {
            tabItem.active = tabItem.id === tab.id;
        });

        // Fire event onSelectTab.
        this.nhTabPanelComponents.forEach((nhTabPanel: NhTabPanelComponent) => {
            if (nhTabPanel.id === tab.id) {
                nhTabPanel.onSelectTab.emit();
            }
        });
    }

    closeTab(tabItem: NHTab) {
        this.onCloseTab.emit({ id: tabItem.id, active: tabItem.active });
        _.remove(this.listTabTitle, (tabTitle: NHTabTitle) => {
            return tabTitle.id === tabItem.id;
        });
        _.remove(this.listTabDynamic, (tabPanel: NHTab) => {
            return tabPanel.id === tabItem.id;
        });

        // Nếu đóng tab đang active -> active vào tab cuối cùng.
        if (tabItem.active) {
            const lastTabItem = this.listTabDynamic[this.listTabDynamic.length - 1];
            if (lastTabItem) {
                this.setTabActive(lastTabItem);
            }
        }
    }

    closeTabByTabId(tabId: string) {
        const tabInfo = _.find(this.listTabDynamic, (tabItem: NHTab) => {
            return tabItem.id === tabId;
        });
        if (tabInfo) {
            this.closeTab(tabInfo);
        }
    }

    setTabActiveById(tabId: string) {
        this.tabService.selectTab(tabId);
        this.listTabTitle.map((tabTitle: NHTabTitle) => {
            tabTitle.active = tabTitle.id === tabId;
        });
    }

    private setTabActive(tab: NHTab) {
        this.listTabTitle.forEach((tabItem: NHTab) => {
            tabItem.active = tabItem.id === tab.id;
        });
        this.tabService.selectTab(tab.id);
    }

    private appendTab(tabItem: NHTab) {
        this.listTabTitle = [...this.listTabTitle, tabItem];
        this.listTabDynamic = [...this.listTabDynamic, tabItem];
        setTimeout(() => {
            const insertedTab = this.nhTabPanelComponentsDynamic.last;
            const componentInstance = insertedTab.loadComponent(tabItem.component);

            // Gọi lại callback sau khi thêm tab mới thành công.
            if (typeof tabItem.callback === 'function') {
                tabItem.callback(componentInstance);
            }

            // Thay đổi trạng thái active vào tab mới được chọn.
            if (tabItem.active) {
                this.setTabActive(tabItem);
            }
        }, 100);
    }
}
