import { Type } from '@angular/core';

/**
 * Created by HoangNH on 6/20/2017.
 */
export class NHTabTitle {
    id: string;
    title: string;
    active: boolean;
    url: string;
    icon: string;

    constructor(id: string, title: string, active?: boolean, url?: string, icon?: string) {
        this.id = id;
        this.title = title;
        this.active = active == null ? false : active;
        this.url = url;
        this.icon = icon;
    }
}

export class NHTab {
    id: any;
    title: string;
    active: boolean;
    icon: string;
    showClose: boolean;
    distince: boolean;
    url: string;
    callback: (component: any) => void;

    constructor(public component: Type<any>, title?: string, active?: boolean, showClose?: boolean, distince?: boolean, icon?: string,
        callback?: (component: any) => void, id?: string, url?: string) {
        this.id = id == null || id === undefined ? this.getTabId() : id;
        this.title = title;
        this.active = active;
        this.icon = icon;
        this.showClose = showClose;
        this.distince = distince == null || distince === undefined ? false : distince;
        this.url = url;

        if (typeof callback === 'function') {
            this.callback = callback;
        }
    }

    private getTabId(): string {
        return 'nh-tabs-' + Math.round(new Date().getTime() + (Math.random() * 100));
    }
}
